<template>
    <div class="form_register_detail">
        <div class="register_header">
			<div class="back" @click="back"></div>
            <div class="logo"><img :src="'http://'+taskLogo" alt=""></div>
            <div class="name">
                <p>{{taskName}}</p>
                <p>{{taskDescription}}</p>
                <p> </p>
            </div>

            <div class="theStatus">
            	<div v-if="this.status=='nostart'"></div>
            	<div v-if="this.status=='onTheWay'"></div>
            	<div v-if="this.status=='start'"></div>
            </div>

            <div class="price">{{reward}}</div>

		</div>
        <div class="bar"></div>
        <div class="box">
            <div class="top">
            	<p>1. </p>
            </div>
            <div class="btm">
                <div class="bz"></div>

                <!--  -->
                <div class="bz_main" v-if="this.status=='nostart'">
                    <div class="tp">
                        <div class="yuan">1</div>
                        <div class="wczz"></div>
                        <div class="addmoney">{{reward}}</div>
                    </div>
                    <div class="btn" @click="goRegister"></div>
                </div>
                <!-- -->
                <div class="bz_main" v-if="this.status=='start'">
                    <div class="tp">
                        <div class="yuan" style="color:#b2b2b2;border:.03125rem solid #b2b2b2">1</div>
                        <div class="wczz" style="color:#b2b2b2"></div>
                        <div class="addmoney" style="color:#b2b2b2">{{reward}}</div>
                    </div>
                    <div class="btn_box">
                        <div class="btn_left" @click="goDoTask"></div>
                    </div>
                </div>
                 <!--  -->
                <div class="bz_main" v-if="this.status=='onTheWay'">
                    <div class="tp">
                        <div class="yuan">1</div>
                        <div class="wczz"></div>
                        <div class="addmoney">{{reward}}</div>
                    </div>
                    <div class="onTway"></div>
                </div>


            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:"FromRegisterTaskDetail",
        data(){
            return{
                //
                isComplete:true,
                task:{},
                status:'',
                //
                taskName:'',
                //
                taskDescription:'',
                //
                reward:'',
                taskLogo:''
            }
        },
        methods: {
            back(){
                this.$router.go(-1)
            },
            //
            goRegister(){

            },
            //
            goDoTask(){
                this.$router.push('/makemoney/jisu/downloadmission/downMissionList')
            },
            //
            receiveAward(){

            }
        },
        beforeMount(){
//          console.log(this.$route.params.id)
        },
        mounted(){
        	let user = localStorage.getItem("userId")
			let taskId = this.$route.params.id
			user = JSON.parse(user)
			this.$http({
						method:'POST',
						url:this.$taskRecordSearch,//
						headers: {
   							 "content-type": "application/json"
						 },
						responseType:'text/plain',
						 params:{
						 		userId: user.id,
						 		taskId:taskId,
						 		taskType:2
						 }
			}).then((respose)=>{				
					this.$http({
							method:'GET',
							url:this.$getTaskDetail+"/"+taskId//
					}).then((res)=>{
						this.task = res.data						
						this.taskName = this.task.data.taskName
						this.reward = this.task.data.reward
						this.taskDescription = this.task.data.taskDescription
						this.taskLogo = this.task.data.taskLogo
						if(respose.data.data.length == 0){//  //,mmp)
							console.log("")
							this.status = 'nostart'
							//		
						}else if(respose.data.data[0].status ==7){//
							console.log("")
							this.status = 'start'
							
						}else if(respose.data.data[0].status ==1){//
							console.log("")
							this.status = 'onTheWay'
						}
				
				})
				

				
			})
        }
    }
</script>

<style lang="scss">
    .form_register_detail{
       .register_header{
          height: 7.55rem;
           width: 100%;
           background: #fae44c;
           overflow: hidden;
           display: flex;
           .back{
              margin-top: .78125rem;
              margin-left: .625rem;
              width: .8125rem;
              height: 1.25rem;
              background: url(../../../../assets/back.png) no-repeat center center;
              background-size: 0.62rem 1.28rem;
           }
           .logo{
               height: 3.75rem;
               width: 3.75rem;
               margin-top:2.65625rem;
               img{height: 100%;width: 100%;}
           }
           .name{
               margin-left: .3125rem;
               margin-top: 2.90625rem;
               font-size: .8125rem;
               color: #000000;
               p{
                   height: 1.0625rem;
                   line-height: 1.0625rem;
               }
                p:nth-child(2){color: #787878};  
                p:nth-child(3){color: #f94b4b};
           }
            .theStatus{
           		position: absolute;
               top: 3.96875rem;
               right: .625rem;
               color: #010101;
           }
           .price{
               position: absolute;
               top: 5.96875rem;
               right: .625rem;
               color: #f94b4b;
           }
       }
       .bar{
           height: .59375rem;
           width: 100%;
           background: #f1f1f1;
       }
       .box{
           height: 11.53rem;
           border-top: .03125rem solid #e4e4e4;
           border-bottom: .0625rem solid #888686;
           box-shadow: 0rem .215rem .625rem #888686;
           .top{
               height: 4.21rem;
               line-height: 1.34375rem;
               text-align: center;
               color: #ff0101;
               border-bottom: .03125rem solid #cdcdcd;
               font-size:0.75rem;
               p{
               	padding: 0 0.62rem;
               	color: #525252;
               	text-align: left;
               	
               }
           }
           .btm{
               width: 100%;
               .bz{
                   margin-left: .9375rem;
                   margin-top: .3125rem;
                   font-size: .8125rem;
                   color: #101010;
               }
               .bz_main{
                   .tp{
                    height: 2.34375rem;
                    padding-top: .5rem;
                    width: 100%;
                    display: flex;
                    .yuan{border: .03125rem solid #f94b4b;border-radius: 100%;
                        color: #f94b4b;height: .875rem;width: .875rem;line-height: .875rem;
                        text-align:center;margin-left: .9375rem;}
                    }
                    .onTway{
                    	
               	width: 100%;
               	height: 3.43rem;
               	text-align: center;
               	line-height: 3.43rem;
               	color: #010101;
               	font-size: 0.9rem;
               
                    }
                    
                    .wczz{font-size: .8125rem;color: #000000;}
                    .addmoney{font-size: .8125rem;color: #000000;position: absolute;right: .625rem;}
                }
                .btn{width: 94.6%;height: 2.25rem;text-align: center;line-height: 2.25rem;background: #f94b4b;
                    margin-left: .625rem;color: #ffffff;border-radius: .3125rem;}
               }    
                .btn_box{
                    height: 2.25rem;line-height: 2.25rem;display: flex; margin-left: .625rem;justify-content: center;
                    .btn_left{width: 20.0625rem;height: 2.25rem;text-align: center;line-height: 2.25rem;background: #f94b4b;
                    color: #ffffff;border-radius: .3125rem;}
                    .btn_right{width: 10.0625rem;height: 2.25rem;text-align: center;line-height: 2.25rem;background: #f94b4b;
                    color: #ffffff;border-radius: .3125rem;margin-left:2.125rem;}
                }
              }
              
    }
</style>


