<template>
	<div class="aboutUs"> 
		<div class="aboutUs_header">
			<div class="back" @click="back"></div>
			<div class="center"></div>
			<div class="block"></div>
		</div>
		<div class="logo">
			
		</div>
		<div class="verssion">
			{{verssion}}
		</div>
		<p class="pOne"></p>
		<div class="to_weiChat"></div>
	</div>
	
</template>

<script>
	export default{
		data(){
			return{
				verssion:"  1.0.0"
			}
		},
		methods:{
			back(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style lang="scss" scoped>
.aboutUs{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background: #f1f1f1;
	.aboutUs_header{				
			  	width: 100%;
	height: 2.85rem;
			  	background: #fae44c;
			  	padding-left: 0.62rem;
			  	padding-right: 0.62rem;
			  	box-sizing: border-box;
			  	display: flex;
			  	justify-content: space-between;
				  	 .back{
				  	width: 2.62rem;
				  	height: 100%;
				  	background: url(../../assets/back.png) no-repeat left center;
				  	background-size: 0.62rem 1.28rem;
				 		 }
				 	.center{
				  	width: 7.61rem;
				  	height: 2.81rem;
				  	text-align: center;
				  	line-height: 2.81rem;
				  	font-size: 1.12rem;
				    color: #72463d;
  							}
					  .block{
					  	width: 2.62rem;
					  }
 	 
		
	}
	.logo{
		width: 6.25rem;
		height: 6.25rem;
		margin: 0 auto;
		margin-top: 2.62rem;
		background: url(../../assets/logo.png) no-repeat center center;
		background-size:  100%;
		border-radius: 18px;
	}
	.verssion{
		width: 100%;
		height: 2.93rem;
		text-align: center;
		line-height: 2.93rem;
		font-size: 0.93rem;
		color: #232323;
	}
	.pOne{
		width: 100%;
		height: 4.31rem;
		padding:0.93rem 1.25rem;
		line-height: 1.25rem;
		font-size: 0.93rem;
		color:#181818 ;
	}
	.to_weiChat{
		width: 10.84rem;
		height: 1.71rem;
		margin: 0.84rem auto;
		background: #f15c23;
		border-radius: 1.56rem;
		text-align: center;
		line-height: 1.71rem;
		font-size: 0.9rem;
		color: #ffffff;
	}
}

</style>


