<template>
    <div class="form_register_detail">
        <div class="register_header">
			<div class="back" @click="back"></div>
            <div class="logo"><img :src="'http://'+taskLogo" alt=""></div>
            <div class="name">{{taskName}}</div>

            
            <div class="theStatus">
            	<div v-if="this.status=='nostart'"></div>
            	<div v-if="this.status=='noget'"></div>
            	<div v-if="this.status=='get'"></div>
            	<div v-if="this.status=='fild'"></div>
            </div>
            <div class="price">{{reward}}</div>
		</div>
        <div class="bar"></div>
        <div class="box">
            <div class="top"></div>
            <div class="btm">
                <div class="bz"></div>

                <!--  -->
                <div class="bz_main" v-if="this.status=='nostart'">
                    <div class="tp">
                        <div class="yuan">1</div>
                        <div class="wczz"></div>
                        <div class="addmoney">+{{reward}}</div>
                    </div>
                    <div class="btn" @click="goRegister"></div>
                </div>
                <!--  -->
                <div class="bz_main" v-if="this.status=='noget'">
                    <div class="tp">
                        <div class="yuan" style="color:#b2b2b2;border:.03125rem solid #b2b2b2">1</div>
                        <div class="wczz" style="color:#b2b2b2"></div>
                        <div class="addmoney" style="color:#b2b2b2">+{{reward}}</div>
                    </div>
                    <div class="btn_box">
                        <div class="btn_left" @click="goDoTask"></div>
                        <div class="btn_right" @click="receiveAward"></div>
                    </div>
                </div>
                <!--  -->
                <div class="bz_main" v-if="this.status=='get'">
                    <div class="tp">
                        <div class="yuan" style="color:#7c7c7c">1</div>
                        <div class="wczz" style="color:#7c7c7c"></div>
                        <div class="addmoney" style="color:#7c7c7c">+{{reward}}</div>
                    </div>
                    <div class="btn" @click="goOthoer"></div>
                </div>
                <!---->
				<div class="onTway" v-if="this.status=='onTheWay'">
					
				</div>
				<!---->
				 <div class="bz_main" v-if="this.status=='fild'">
                    <div class="tp">
                        <div class="yuan" style="color:#b2b2b2;border:.03125rem solid #b2b2b2">1</div>
                        <div class="wczz" style="color:#b2b2b2"></div>
                        <div class="addmoney" style="color:#b2b2b2">+{{reward}}</div>
                    </div>
                    <div class="btn_box">
                        <div class="btn_left"></div>
                        <div class="btn_right" @click="to_sendPhoto"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:"FromRegisterTaskDetail",
        data(){
            return{
                //(nostart/noget/get) 
                status:'',
                task:{},
                taskName:'',
                reward:'',
                taskLogo:''
            }
        },
        methods: {
            back(){
                this.$router.go(-1)
            },
            //
            goRegister(){

            },
            //
            goDoTask(){
                this.$router.push('/makemoney/jisu/fromRegister/myformMession')
            },
            //
            receiveAward(){
			 this.$router.push('/makemoney/jisu/fromRegister/formMessionList/sendPhoto')				
            },
            //
            goOthoer(){
				this.$router.push('/makemoney/jisu/fromRegister/formMessionList')
            },
            //
            to_sendPhoto(){
            	this.$router.push('/makemoney/jisu/fromRegister/formMessionList/sendPhoto') 
            }
        },
        mounted(){
			let user = localStorage.getItem("userId")
			let taskId = this.$route.params.id
			user = JSON.parse(user)
			this.$http({
						method:'POST',
						url:this.$taskRecordSearch,
						headers: {
   							 "content-type": "application/json"
						},
						 params:{
						 		userId: user.id,
						 		taskId:taskId,
						 		taskType:1
						 }
			}).then((repose)=>{
//				console.log(repose.data.data[0])
				this.$http({
							method:'GET',
							url:this.$getTaskDetail+"/"+taskId,
					}).then((res)=>{			
							this.task = res.data;
							this.taskName=this.task.data.taskName
							this.reward = this.task.data.reward
							this.taskLogo = this.task.data.taskLogo

							//$taskRecordSearch   							
							if(repose.data.data.length == 0){//  
//							console.log("")
							this.status = 'nostart'

						}else if(repose.data.data[0].status == 1){//
							this.status = "noget"
//							console.log("")
							
						}else if(repose.data.data[0].status ==7 ){// 
//							console.log("")
							this.status = "get"
						}else if(repose.data.data[0].status ==6){//     
//							console.log("")
							this.status = 'fild'
						}else if(repose.data.data[0].status ==5){// 
//							console.log("")
							this.status = 'onTheWay'
							
						}
							
						})
	
					})
	
		
		
        	
        }
    }
</script>

<style lang="scss" scoped>
    .form_register_detail{
       .register_header{
           height: 7.55rem;
           width: 100%;
           background: #fae44c;
           overflow: hidden;
           display: flex;
           .back{
              margin-top: .78125rem;
              margin-left: .625rem;
              width: .8125rem;
              height: 1.25rem;
              background: url(../../../../assets/back.png) no-repeat center center;
              background-size: 0.62rem 1.28rem;
           }
           .logo{
               height: 3.75rem;
               width: 3.75rem;
               margin-top:2.65625rem;
               img{height: 100%;width: 100%;}
           }
           .name{
               margin-left: .3125rem;
               margin-top: 2.90625rem;
               font-size: .8125rem;
               color: #000000;
           }
           .theStatus{
           		position: absolute;
               top: 3.96875rem;
               right: .625rem;
               color: #010101;
           }
           .price{
               position: absolute;
               top: 5.96875rem;
               right: .625rem;
               color: #f94b4b;
           }
       }
       .bar{
           height: .59375rem;
           width: 100%;
           background: #f1f1f1;
       }
       .box{
           height: 7.90625rem;
           border-top: .03125rem solid #e4e4e4;
           border-bottom: .0625rem solid #888686;
           box-shadow: 0rem .215rem .625rem #888686;
           .top{
               height: 1.34375rem;
               line-height: 1.34375rem;
               text-align: center;
               color: #ff0101;
               border-bottom: .03125rem solid #cdcdcd;
           }
           .btm{
               width: 100%;
               .bz{
                   margin-left: .9375rem;
                   margin-top: .3125rem;
                   font-size: .8125rem;
                   color: #101010;
               }
               .onTway{
               	width: 100%;
               	height: 3.43rem;
               	text-align: center;
               	line-height: 3.43rem;
               	color: #010101;
               	font-size: 0.9rem;
               }
               .bz_main{
                   .tp{
                    height: 2.34375rem;
                    padding-top: .5rem;
                    width: 100%;
                    display: flex;
                    .yuan{border: .03125rem solid #f94b4b;border-radius: 100%;
                        color: #f94b4b;height: .875rem;width: .875rem;line-height: .875rem;
                        text-align:center;margin-left: .9375rem;}
                    }
                    .wczz{font-size: .8125rem;color: #000000;}
                    .addmoney{font-size: .8125rem;color: #000000;position: absolute;right: .625rem;}
                }
                .btn{width: 94.6%;height: 2.25rem;text-align: center;line-height: 2.25rem;background: #f94b4b;
                    margin-left: .625rem;color: #ffffff;border-radius: .3125rem;}
               }    
                .btn_box{
                    height: 2.25rem;line-height: 2.25rem;display: flex; margin-left: .625rem;
                    .btn_left{width: 10.0625rem;height: 2.25rem;text-align: center;line-height: 2.25rem;background: #f94b4b;
                    color: #ffffff;border-radius: .3125rem;}
                    .btn_right{width: 10.0625rem;height: 2.25rem;text-align: center;line-height: 2.25rem;background: #f94b4b;
                    color: #ffffff;border-radius: .3125rem;margin-left:2.125rem;}
                }
              }
              
    }
</style>


