import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import Login from '@/components/login'
import Register from '@/components/Register'
// 
import RedBoxSuccess from '@/components/RedBoxSuccess'
//
import UserAgreement from '@/components/UserAgreement'
import MakeMoney from '@/components/Money_main/MakeMoney'
import Discount from '@/components/FindDiscounts/Discount'
import BanKa from '@/components/Money_main/banka/BanKa'
import JiSu from '@/components/Money_main/jisu/JiSu'
import Download from '@/components/Money_main/jisu/Download'
import ShuaDan from '@/components/Money_main/shuadan/ShuaDan'
import ForgetPsw from '@/components/ForgetPsw'
//
import MyInfo from '@/components/My/MyInfo'
import Setting from '@/components/My/Setting'
import ChangePsw from '@/components/My/ChangePsw'
import HelpCenter from '@/components/My/HelpCenter'
import AboutUs from '@/components/My/AboutUs'
	//
import BindWx from'@/components/my/bindAll/BindWx'
//
import DrawCash from '@/components/My/drawCash/DrawCash'
import DrawCashRecord from '@/components/My/drawCash/DrawCashRecord'
import DrawCashSubmitted from '@/components/My/drawCash/DrawCashSubmitted'

import WeichatDrawCash from '@/components/My/drawCash/WeichatDrawCash'
import AlipayDrawCash from '@/components/My/drawCash/AlipayDrawCash'
import PhoneBill from '@/components/My/drawCash/PhoneBill'

//
import BindAlipay from '@/components/my/bindAll/BindAlipay'
import BindNewPhone from'@/components/my/bindAll/BindNewPhone'
import ChangeBind from '@/components/my/bindAll/ChangeBind'
import VerifyNumber from '@/components/my/bindAll/VerifyNumber'
//
import Coupon from '@/components/my/useit/Coupon'
import DiscountCoupon from '@/components/my/useit/DiscountCoupon'
import FreeLesson from '@/components/my/useit/FreeLesson'
//
import HsTyCoupon from '@/components/my/historyCoupon/HsTyCoupon'
import HsTyDiscount from '@/components/my/historyCoupon/HsTyDiscount'
import HsTyFree from '@/components/my/historyCoupon/HsTyFree'
import ChangePhoneSucceed  from '@/components/my/bindAll/ChangePhoneSucceed'
// ()
import TaskRecordMain  from '@/components/my/taskRecord/taskRecordMain'
import TaskRecordJs from '@/components/my/taskRecord/taskRecordJs'
import TaskRecordBk from '@/components/my/taskRecord/taskRecordBk'
import TaskRecordJsZc from '@/components/my/taskRecord/taskRecordJsZc'
import TaskRecordJsXz from '@/components/my/taskRecord/taskRecordJsXz'
// 
import DiscountRecordMain  from '@/components/my/discountRecord/discountRecordMain'
import DiscountRecordTicket from '@/components/my/discountRecord/discountRecordTicket'
import DiscountRecordClass from '@/components/my/discountRecord/discountRecordClass'
import DiscountRecordBuy from '@/components/my/discountRecord/discountRecordBuy'
import DiscountRecordShop from '@/components/my/discountRecord/discountRecordShop'
//
import FormRegister from '@/components/Money_main/jisu/formRegister/FormRegister'
import FormMessionList from'@/components/Money_main/jisu/formRegister/FormMessionList'
import MyFormMession from'@/components/Money_main/jisu/formRegister/MyFormMession'
import SendPhoto from '@/components/Money_main/jisu/formRegister/SendPhoto'
import ApplyRecord from '@/components/Money_main/jisu/formRegister/ApplyRecord'
//
import FormRegisterTaskDetail from '@/components/Money_main/jisu/formRegister/FormRegisterTaskDetail'
//
import DownloadTaskDetail from '@/components/Money_main/jisu/downloadMission/DownloadTaskDetail'
import DownLoadMission from '@/components/Money_main/jisu/downloadMission/DownloadMission'
import DownLoadMissionList from'@/components/Money_main/jisu/downloadMission/DownloadMissionList'
import MyMission from'@/components/Money_main/jisu/downloadMission/MyMission'
//
import GetCardMission from '@/components/Money_main/banka/getCard/GetcardMission'
import GetcardMissionList from'@/components/Money_main/banka/getCard/GetcardMissionList'
import MyGetcardMission from'@/components/Money_main/banka/getCard/MyGetcardMission'
import ReservedInformation from '@/components/Money_main/banka/getCard/reservedInformation'

//
import SuggestPlay from '@/components/Money_main/suggestplay/SuggestPlay'
import SuggestPlayList from '@/components/Money_main/suggestplay/SuggestPlayList'
import MySuggestPlay from '@/components/Money_main/suggestplay/MySuggestPlay'

//
import FreeClass from '@/components/FindDiscounts/FreeClass'
import DiscountTicket from '@/components/FindDiscounts/DiscountTicket'
import DiscountShop from '@/components/FindDiscounts/DiscountShop'
import OYuanBuy from '@/components/FindDiscounts/OYuanBuy'

//
import Third from '@/components/ThirdPart'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      redirect:'/makemoney',
      children:[
			       {
						path:'/makemoney',
						component: MakeMoney,
						redirect: "/makemoney/jisu",
						children:[
										{path:'/makemoney/jisu',
										name:'jisu',
										component:JiSu
									},
										{path:'/makemoney/banka',
										name:'banka',
										component:BanKa},
										{
										path:'/makemoney/Download',
										name:'download',
										component:Download
									},
										{path:'/makemoney/shuadan',
										name:'shuadan',
										component:ShuaDan}
										]
					},
					//
				{path:'/discount',
				name:'discount',
				component:Discount},
				//
				{path:'/my',
				name:'my',
				component:MyInfo}
    		  ]
	},
		//
		{
			path:'/login',
			name:'Login',
			component:Login
		},
		//
		{
			path:'/register',
			name:'Register',
			component:Register
		},
				//
				{
					path:'/third',
					name:'Third',
					component:Third
				},
		//
		{
			path:'/userAgreement',
			name:'userAgreement',
			component:UserAgreement
		},
		//
		{
			path:'/forgetpsw',
			name:'Forgetpsw',
			component:ForgetPsw
		},
		// 
		{
		  path:'/redBoxSuccess',
		  component:RedBoxSuccess
		},
		//
		{	
			path:'/downloadTaskDetail/:id',
			name:'DownloadTaskDetail',
			component:DownloadTaskDetail
		},
		//
		{
			path:'/formRegisterTaskDetail/:id',
			name:'FormRegisterTaskDetail',
			component:FormRegisterTaskDetail
		},
		// 
		{
			path:'/makemoney/jisu/fromRegister',
			name:"fromRegister",
			component:FormRegister,
			redirect: "/makemoney/jisu/fromRegister/formMessionList",
			children:[
		
										{path:'/makemoney/jisu/fromRegister/formMessionList',
										name:'formMessionList',
										component:FormMessionList
								},
								{path:'/makemoney/jisu/fromRegister/myformMession',
										name:'myformMession',
										component:MyFormMession
								}
							]
		},
		//
		{			
			path:'/makemoney/jisu/fromRegister/formMessionList/sendPhoto',
			name:"sendPhoto",
			component:SendPhoto
		},
		//
		{			
			path:'/makemoney/jisu/fromRegister/formMessionList/sendPhoto/applyRecord',
			name:"applyRecord",
			component:ApplyRecord
		},
		//  
		{
			path:'/makemoney/jisu/downloadmission',
			component:DownLoadMission,
			redirect: "/makemoney/jisu/downloadmission/downMissionList",
			children:[
		
										{path:'/makemoney/jisu/downloadmission/downMissionList',
										name:'DownLoadMissionList',
										component:DownLoadMissionList
								},
								{path:'/makemoney/jisu/downloadmission/myMission',
										name:'MyMission',
										component:MyMission
								}
							]
		},		
		//
		{
			path:'/makemoney/banka/getcardmission',
			component:GetCardMission,
			redirect: "/makemoney/banka/getcardmission/getcardMissionList",
			children:[
		
										{path:'/makemoney/banka/getcardmission/getcardMissionList',
										name:'GetcardMissionList',
										component:GetcardMissionList
								},
								{path:'/makemoney/banka/getcardmission/myGetcardMission',
										component:MyGetcardMission,
										name:'MyGetcardMission'
								}
							]
		},
		//
		{
			path:'/makemoney/banka/getcardmission/reservedInformation/:id',
			component:ReservedInformation
		},
		//
		{
			path:'/makemoney/suggestplay',
			component:SuggestPlay,
			redirect: "/makemoney/suggestplay/suggestplayList",
			children:[
		
										{path:'/makemoney/suggestplay/suggestplayList',
										name:'suggestplayList',
										component:SuggestPlayList
								},
								{path:'/makemoney/suggestplay/mysuggestplay',
								name:'mysuggestplay',
										component:MySuggestPlay
								}
							]
		},
		//
		{
			path:'/my/setting',
			name:'setting',
			component:Setting
		},
		//
		{
			path:'/my/helpcenter',
			name:'HelpCenter',
			component:HelpCenter
		},
		//
		{
			path:'/my/aboutUs',
			name:'AboutUs',
			component:AboutUs
		},
		//
		{
			path:'/my/setting/bindAlipay',
			name:'bindAlipay',
			component:BindAlipay
		},
		//
		{
			path:'/my/setting/bindWx',
			name:'bindWx',
			component:BindWx
		},
		//
		{
			path:'/my/setting/verifyNumber',
			name:'verifyNumber',
			component:VerifyNumber
		},
		//
		{			
			path:'/my/setting/verifyNumber/ChangeBind',
			name:'ChangeBind',
			component:ChangeBind
		},
		//
		{
			path:'/my/setting/verifyNumber/ChangeBind/BindNewPhone',
			name:'BindNewPhone',
			component:BindNewPhone
		},
		//
		{
			path:'/my/setting/verifyNumber/ChangeBind/ChangePhoneSucceed',
			name:'ChangePhoneSucceed',
			component:ChangePhoneSucceed
		},
		//taskRecord
		{
			path:'/my/setting/TaskRecordMain',
			name:'TaskRecordMain',
			component:TaskRecordMain,
			redirect:'/my/setting/TaskRecordMain/taskjisu/zc',
						children:[
							    //_
								{path:'/my/setting/TaskRecordMain/taskjisu',
								name:'TaskRecordJs',
								component:TaskRecordJs,
								redirect:'/my/setting/TaskRecordMain/taskjisu/zc',
								children:[
									{path:'/my/setting/TaskRecordMain/taskjisu/zc',
									name:'TaskRecordJsZc',
									component:TaskRecordJsZc
								    },
									{path:'/my/setting/TaskRecordMain/taskjisu/xz',
									name:'TaskRecordJsXz',
									component:TaskRecordJsXz}
								]
								},
								//_
								{path:'/my/setting/TaskRecordMain/taskbanka',
								name:'TaskRecordBk',
								component:TaskRecordBk}
								]
		},
		//
		{
			path:'/my/setting/DiscountRecordMain',
			name:'DiscountRecordMain',
			component:DiscountRecordMain,
			redirect:"/my/setting/DiscountRecordMain/DiscountRecordTicket",
			children:[
				{path:'/my/setting/DiscountRecordMain/DiscountRecordTicket',
				name:'DiscountRecordTicket',
				component:DiscountRecordTicket
				},
				{path:'/my/setting/DiscountRecordMain/DiscountRecordClass',
				name:'DiscountRecordClass',
				component:DiscountRecordClass
				},
				{path:'/my/setting/DiscountRecordMain/DiscountRecordBuy',
				name:'DiscountRecordBuy',
				component:DiscountRecordBuy
				},
				{path:'/my/setting/DiscountRecordMain/DiscountRecordShop',
				name:'DiscountRecordShop',
				component:DiscountRecordShop
				}
			]
		},
		//
		{
			path:'/my/changePsw',
			name:'changePsw',
			component:ChangePsw
		},
		//DrawCash
		{
			path:'/my/drawCash',
			name:'drawCash',
			component:DrawCash
		},
		//
		{
			path:'/my/drawCash/submitted',
			name:'drawCashSubmitted',
			component:DrawCashSubmitted
		},
		//
		{
			path:'/my/drawCash/drawCashRecord',
			name:'drawCashRecord',
			component:DrawCashRecord
		},
		//
		{
			path:'/my/drawCash/weichatDrawCash',
			name:'WeichatDrawCash',
			component:WeichatDrawCash
		},
		//
		{
			path:'/my/drawCash/alipayDrawCash',
			name:'alipayDrawCash',
			component:AlipayDrawCash
		},
		//
		{
			path:'/my/drawCash/phoneBill',
			name:'phoneBill',
			component:PhoneBill
		},
		//
		{
			path:'/my/coupon',
			name:'Coupon',
			redirect:'/my/coupon/discountCoupon',
			component:Coupon,
			children:[
				{
					path:'/my/coupon/discountCoupon',
					name:'DiscountCoupon',
					component:DiscountCoupon
				},
				{
					path:'/my/coupon/freeLesson',
					name:'FreeLesson',
					component:FreeLesson
				}
			]
		},
		//
		{
			path:'/my/coupon/historyCoupon',
			name:'HsTyCoupon',
			redirect:'/my/coupon/historyCoupon/HsTyDiscount',
			component:HsTyCoupon,
			children:[
				{
					path:'/my/coupon/historyCoupon/HsTyDiscount',
					name:'HsTyDiscount',
					component:HsTyDiscount
				},
				{
					path:'/my/coupon/historyCoupon/HsTyFree',
					name:'HsTyFree',
					component:HsTyFree
				}
			]
		},
		//
		{
			path:'/discount/freeclass',
			name:'freeClass',
			component:FreeClass
			},
		//
		{
			path:'/discount/ticket',
			name:'DiscountTicket',
			component:DiscountTicket
			},
		//0
		{
			path:'/discount/buy',
			name:'OYuanBuy',
			component:OYuanBuy
			},
		//
		{
			path:'/discount/shop',
			name:'DiscountShop',
			component:DiscountShop
			}
  ]
})



